<template>
  <div>
    <CRow class="clapans">
      <CCol md="12">
        <CCard>
          <CCardHeader>
            <div class="d-flex aligin-items-center justify-content-between">
              <span>История Мастер Хаба: {{ id }}</span>
              <CButton
                color="success"
                class="ml-3"
                @click="addCommentModal = !addCommentModal"
              >
                Добавить комметарий Хаба
              </CButton>
              <CButton color="success" class="mr-2" @click="downloadArray">
                Скачать историю Мастер Хаба в csv
              </CButton>
            </div>
          </CCardHeader>
          <CCardBody>
            <CDataTable
              class="mb-0 table-outline"
              hover
              :items="tableItems"
              :fields="tableFields"
              head-color="light"
            >
              <td slot="select" slot-scope="{ item }">
                <CInputCheckbox
                  :value="item.select"
                  custom
                  inline
                  @change="isSelect = true"
                />
              </td>
              <td slot="city" slot-scope="{ item }">
                {{ item.city }}
              </td>
              <td slot="district" slot-scope="{ item }">
                {{ item.district }}
              </td>
              <td slot="street" slot-scope="{ item }">
                {{ item.street }}
              </td>
              <td slot="building" slot-scope="{ item }">
                {{ item.typeofbuilding }}
              </td>
              <td slot="numHouse" slot-scope="{ item }">
                {{ item.buildingnum }}
              </td>
              <td slot="numDoor" slot-scope="{ item }">
                {{ item.entrancenum }}
              </td>
              <td slot="numMine" slot-scope="{ item }">
                {{ item.shaftnum }}
              </td>
              <td slot="stage" slot-scope="{ item }">
                {{ item.floor }}
              </td>
              <td slot="numOffice" slot-scope="{ item }">
                {{ item.aptoroffice }}
              </td>
              <td slot="id" slot-scope="{ item }">
                {{ item.valveID }}
              </td>
              <td slot="status" slot-scope="{ item }">
                <CBadge v-if="item.status == 0" color="danger">
                  выключен
                </CBadge>
                <CBadge v-else-if="item.status == 1" color="success"
                  >включен</CBadge
                >
                <CBadge v-else-if="item.status == 2" color="warning"
                  >нет данных</CBadge
                >
                <CBadge v-else color="info">не известно</CBadge>
                <br />
                <CSwitch
                  class="mt-1"
                  color="primary"
                  :checked.sync="item.status == 1"
                  :value="item.status"
                  @update:checked="actionStatusChange(item)"
                />
              </td>
              <td slot="settings" slot-scope="{ item }">
                <span
                  style="white-space: nowrap; cursor: pointer"
                  class="d-flex align-items-center"
                  @click="warningModal = !warningModal"
                  ><CIcon name="cilSettings" class="mr-1" /> Настройки
                </span>
              </td>
            </CDataTable>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
    <CModal
      title="Добавление комментария"
      :ok-disabled="true"
      :show.sync="addCommentModal"
    >
      <h4>Введите комметарий</h4>
      <div class="mt-4">
        <CTextarea type="text" v-model="commentary" />
      </div>
      <div>
        <CButton :disabled="!commentary" color="success" class="mr-3" @click="sendCommentary"
          >Отправить</CButton
        >
      </div>
      <div slot="footer"></div>
    </CModal>
  </div>
</template>

<script>
import MainChartExample from "./charts/MainChartExample";
import WidgetsDropdown from "./widgets/WidgetsDropdown";
import WidgetsBrand from "./widgets/WidgetsBrand";
import downloadCsv from "download-csv";

const qs = require("qs");
const axios = require("axios");
export default {
  name: "Master",
  components: {
    MainChartExample,
    WidgetsDropdown,
    WidgetsBrand,
  },
  data() {
    return {
      addCommentModal: false,
      commentary: "",
      warning: false,
      success: false,
      alert_message: "",
      warningModal: false,
      isSelect: false,
      action_switcher: "",
      id: this.$route.params.id,
      tableItems: [],
      tableFields: [
        { key: "masterhubID", label: "ID" },
        { key: "comment", label: "Комметарий" },
        { key: "date", label: "Дата" },
      ],
    };
  },
  mounted() {
    this.getResults();
  },
  methods: {
    getResults() {
      let app = this;
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios
        .get("/getmasterhubcommentsbyID/" + app.$route.params.id)
        .then((response) => {
          app.tableItems = response.data;
          for (var i = 0; i < app.tableItems.length; i++) {
            app.tableItems[i].select = false;
          }
        })
        .catch((error) => {
          if (error.response.status == 401) {
            window.auth.logout();
          }
        });
    },
    downloadArray() {
      const datas = this.tableItems;
      const current = new Date();
      const date = `${current.getDate()}-${
        current.getMonth() + 1
      }-${current.getFullYear()}`;
      let name = "История клапана № " + this.id + "_" + date + ".csv";
      downloadCsv(datas, this.tableFields, name);
    },
    sendCommentary() {
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios({
        method: "post",
        url: "/addmasterhubcomment/",
        data: qs.stringify({
          comment: this.commentary,
          ID: this.$route.params.id,
        }),
      })
        .then(() => {
          this.addCommentModal = false;
          this.warningModal = false;
          this.success = true;
          this.alert_message = "Успешно добавлено";
          this.getResults();
          this.setting = {};
          setTimeout(function () {
            this.success = false;
          }, 3000);
        })
        .catch(function (error) {
          this.warning = true;
          this.alert_message = error.response.data.detail;
          setTimeout(function () {
            this.warning = false;
          }, 3000);
        });
    },
  },
};
</script>
